















import {Component, Prop, Vue} from "vue-property-decorator"
import {IBooking} from "@chaosinsight/yogipeople-portalclient"
import i18n from "../i18n"
import Portal from "@/managers/portal"
import { TranslateResult } from "vue-i18n"

@Component({ components: { } })
export default class ListUpcoming extends Vue {
	@Prop()
	public title!: string
	@Prop()
	public bookings!: IBooking[]

	@Prop()
	public selectedBookingId!: number

	@Prop()
	private canceledBookingId!: number
	private sessionTickets!: number
	private modalType: string | null = ""
	private daysInWeek: TranslateResult[] = [i18n.t("sunday"), i18n.t("monday"),
		i18n.t("tuesday"), i18n.t("wednesday"), i18n.t("thursday"),
		i18n.t("friday"), i18n.t("saturday")]
		private timeUntilBooking(id: number) {
		const booking = this.bookings.find(el => el.Id === id)
		this.$emit("timeUntilBooking", booking)
		}
		private created(): void {
			this.getSessionTickets()
			}
		private getTime(time: string) {
			const d = this.getDayLabel(time)
			const h = new Date(time).getHours()
			const m = new Date(time).getMinutes()
			return `${d} ${this.amPm(h, m)}`
			}
		private getSessionTickets(): void {
			Portal.client.call.sessionTickets.get()
			.response
			.then(res => {
				this.sessionTickets = res.length
				this.modalType = "success"
			})
		}
	/**
	 * @returns day label i.e. Today, Tomorrow, Friday...
	 */

	private getDayLabel(day: string): string {
		const date = new Date(day)
		const today = new Date()
		const tomorrow = new Date()
		tomorrow.setDate(today.getDate() + 1)

		if (date.getDate() === today.getDate())
			return i18n.t("today").toString()
		else if (date.getDate() === tomorrow.getDate())
			return i18n.t("tomorrow").toString()
		return `${this.daysInWeek[new Date(day).getDay()]} ${new Date(day).getMonth() + 1}/${new Date(day).getDate()}`
	}

	/**
	 * Convert hour in am/pm format
	 */

	private amPm(hour: number, min: number): string {
		const m = (min && min < 10) ? this.leadZero(min) : min
		if (hour > 12)
			return `${hour % 12}${m ? ":" + m : ""}pm`
		else if (hour < 12)
			return `${hour}${m ? ":" + m : ""}am`
		return `12${m ? ":" + m : ""}pm`
	}

	/**
	 * Lead zero
	 */

	private leadZero = (x: number): string => x < 10 ? x < 0 ? `00` :	`0${x}` : `${x}`
}
